import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { SidebarComponent } from '../../shared/sidebar/sidebar.component';
import { MaterialsComponent } from './index-material/materials.component';
import { EditMaterialComponent } from './edit-material/edit-material.component';

const routes: Routes = [{
  path: '',
  component: SidebarComponent,
  children: [
    { path: '', component: MaterialsComponent },
    { path: 'create-material', component: EditMaterialComponent },
    { path: 'edit-material/:id', component: EditMaterialComponent },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class MaterialsRoutingModule {
  static components = [
    MaterialsComponent,
    EditMaterialComponent  
  ]
}
