import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { MaterialsServices } from '../../../services/materials/materials.services';
import { ActivatedRoute, Router } from '@angular/router';
import { MaterialColumn } from './materials.column';
import { GridDataResult } from '@progress/kendo-angular-grid';
import { NbDialogService, NbToastrService } from '@nebular/theme';
import { FilterToQueryService } from '../../../services/filter-to-query/filter-to-query.service';
import { State } from '@progress/kendo-data-query';
import { SharedApiServiceService } from '../../../services/shared/shared-api-service.service';
import { saveAs } from 'file-saver';

@Component({
    selector: 'app-materials',
    templateUrl: 'materials.component.html',
    styleUrls: ['materials.component.scss'],
})
export class MaterialsComponent implements OnInit {
    @ViewChild('dialog') dialog: any;

    columnGrid: any = MaterialColumn;
    dataItem: GridDataResult = {
        data: [],
        total: 0,
    };
    lastMeta: string;
    loading: boolean;
    autoCompleteData: any = {
        name: 'category_brand',
        list: []
    };
    currentDeleteMaterial: any;
    isLoading: boolean = false;

    constructor(
        private materialsService: MaterialsServices,
        private toaster: NbToastrService,
        // private activeRouter: ActivatedRoute,
        private convertFilter: FilterToQueryService,
        private router: Router,
        private dialogService: NbDialogService,
        private sharedService: SharedApiServiceService
    ) {
    }

    ngOnInit() {
        this.getMaterials();
    }

    getMaterials(meta?: string) {
        this.isLoading = true;
        this.lastMeta = meta;
        this.materialsService.getMaterials(meta).subscribe(
            res => {
                this.dataItem.data = res['data'];
                this.dataItem.total = 4;
                // this.dataItem.total = res['total'];
            },
            error => {
                this.isLoading = false;
            },
            () => {
                this.isLoading = false;
            }
        );
    }

    deleteMaterial(dialogRef: any) {
        this.materialsService.deleteMaterial(this.currentDeleteMaterial.id).subscribe(
            res => {
                this.toaster.show(res['msg'], 'Success', { status: 'success' });
            },
            error => {
                this.toaster.show(error.error.message, 'Error', { status: 'danger' });
                dialogRef.close();
            },
            () => {
                dialogRef.close();
                this.getMaterials(this.lastMeta);
                this.currentDeleteMaterial = null;
            },
        );
    }

    commandActionClick(event: any) {
        switch (event.action.action) {
            case 'edit':
                this.router.navigate(['./materials/edit-material/' + event.data.id]);
                break;

            case 'delete':
                this.currentDeleteMaterial = event.data;
                this.open(this.dialog);
                break;
        }
    }

    open(dialog: TemplateRef<any>) {
        this.dialogService.open(dialog);
    }

    filterChange(event: State) {
        this.getMaterials(this.convertFilter.convertToQuery(event));
    }

    export() {
        this.sharedService.exportUrl = this.lastMeta ? 'materials/export/excel?' + this.lastMeta : 'materials/export/excel';
        this.sharedService.exportData(this.sharedService.exportUrl).subscribe(data => {
            saveAs(data, 'materials.xlsx');
        }),
            error => this.toaster.show('Error downloading the file.', 'Error', { status: 'danger' });
    }
}
